import React from "react"
import { Link } from 'gatsby'
import '../styles/header.scss'
import styled from 'styled-components'

const HeaderContainer = styled.header`
  color: ${props => props.color};
  .center {
    ul {
      .hoverable:before, .hoverable:after {
            content: '';
            position: absolute;
            width: 0%;
            height: 1px;
            top: 50%;
            margin-top: -0.5px;
            background: #fff;
        }
        
        .hoverable:before {
            left: -2.5px;
        }
        .hoverable:after {
            right: 2.5px;
            background: ${props => props.color};
            transition: width 0.8s cubic-bezier(0.22, 0.61, 0.36, 1);
        }
        
        .hoverable:hover:before {
            background: ${props => props.color};
            width: 100%;
            transition: width 0.5s cubic-bezier(0.22, 0.61, 0.36, 1);
        }
        
        .hoverable:hover:after {
            background: transparent;
            width: 100%;
            transition: 0s;
        }
    }
  }

  
`

const Header = ({fontColor, location}) => {
  return (
    <HeaderContainer id='header' color={fontColor}>
      <div className='center'>
        <p><Link to='/'>Karolina Włoszek</Link></p>
        <ul>
          <li>
            <Link to={location !== '/projects/' ? '/projects/' : '/'} className={location !== '/projects/' ? 'hoverable' : null}>
              {location !== '/projects/' ? 'PROJEKTY' : 'X'}
            </Link>
          </li>
          <li>
            <Link to={location !== '/about-me/' ? '/about-me/' : '/'} className={location !== '/about-me/' ? 'hoverable' : null}>
              {location !== '/about-me/' ? 'O MNIE' : 'X'}
            </Link>
          </li>
        </ul>
      </div>
    </HeaderContainer>

  )
}



export default Header